
#nickname{
    min-width: 100%;
    padding: 12px 20px;
    margin: 30px 0;
    box-sizing: border-box;
    border-color:aquamarine ;
    border-radius: 25px;
    outline: none;
}

#juml{
    padding: 0px 30px;
